.ChatBot{position:fixed;bottom:1rem;right:1rem;z-index:1000}.chatToggle{background:#6a1b9a;color:#fff;border:none;border-radius:50%;width:60px;height:60px;cursor:pointer}.chatWindow{width:300px;height:400px;background:#fff;border:1px solid #ddd;border-radius:8px;box-shadow:0 4px 8px rgba(0,0,0,.1);overflow:hidden;display:flex;flex-direction:column}.chatMessages{flex:1;padding:1rem;overflow-y:auto}.botMessage{background:#f0f0f0;padding:.5rem;border-radius:8px;margin-bottom:.5rem}.userMessage{background:#6a1b9a;color:#fff;padding:.5rem;border-radius:8px;align-self:flex-end;margin-bottom:.5rem}.chatInput{display:flex;border-top:1px solid #ddd}.chatInput input{flex:1;padding:.5rem;border:none}.chatInput button{padding:.5rem 1rem;background:#6a1b9a;color:#fff;border:none;cursor:pointer}